<template>
    <el-dialog title="" :visible.sync="dialogVisible" @close="closeDialog" append-to-body >
      <div class="cropper-content">
        <div class="cropper" style="text-align:center">
            <vueCropper
                ref="cropper"
                :img="option.img"
                :outputSize="option.size"
                :outputType="option.outputType"
                :info="true"
                :full="option.full"
                :canMove="option.canMove"
                :canMoveBox="option.canMoveBox"
                :original="option.original"
                :autoCrop="option.autoCrop"
                :fixed="option.fixed"
                :fixedNumber="option.fixedNumber"
                :centerBox="option.centerBox"
                :infoTrue="option.infoTrue"
                :fixedBox="option.fixedBox"
            ></vueCropper>
            <div class="operation" v-show="fixedControl">
                <h3></h3>
                <el-radio-group v-model="fixedType" @change="changeFixedType">
                    <el-radio :label="4"></el-radio>
                    <el-radio :label="1">16:9</el-radio>
                    <el-radio :label="2">4:3</el-radio>
                    <el-radio :label="3">1:1</el-radio>
                </el-radio-group>
            </div>
        </div>
        <div class="img_name" v-show="nameControl">
            <span></span>
            <el-input v-model="fileName" placeholder=""></el-input>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button type="primary" @click="finish" :loading="loading"></el-button>
      </div>
    </el-dialog>
</template>
<script>
import { VueCropper } from 'vue-cropper'
export default {
    name: 'lhc-cropper-dialog',
    props: {
        fixedControl: {
            type: Boolean,
            default: true,
        },
        nameControl: {
            type: Boolean,
            default: true,
        },
        canScale: {
            type: Boolean,
            default: false,
        },
        autoCrop: {
            type: Boolean,
            default: true,
        },
        fixedBox: {
            type: Boolean,
            default: false,
        },
        fixed: {
            type: Boolean,
            default: false,
        },
        fixedNumber: {
            type: Array,
            default: function() {
                return [16, 9]
            }
        },
        full: {
            type: Boolean,
            default: false,
        },
        canMoveBox: {
            type: Boolean,
            default: true,
        },
        original: {
            type: Boolean,
            default: false,
        },
        centerBox: {
            type: Boolean,
            default: true,
        },
        infoTrue: {
            type: Boolean,
            default: true,
        },
    },
    data: function() {
        return {
            dialogVisible: false,
            fixedType: 4,
            // option
            option: {
                img: '', // 
                info: true, // 
                outputSize: 0.8, // 
                outputType: 'png', // 
                canScale: false, // 
                autoCrop: true, // 
                // autoCropWidth: 300, // 
                // autoCropHeight: 200, // 
                fixedBox: false, //  
                fixed: false, // 
                fixedNumber: [7, 5], // 
                full: false, // 
                canMoveBox: true, // 
                original: false, // 
                centerBox: true, // 
                infoTrue: true // true  false 
            },
            fileName: '',
            // 
            loading: false
        }
    },
    components: {
        VueCropper,
    },
    methods: {
        openDialog(file) {
            const _that = this
            _that.option = {
                ..._that.option,
                canScale: _that.canScale,
                autoCrop: _that.autoCrop,
                fixedBox: _that.fixedBox,
                fixed: _that.fixed,
                fixedNumber: _that.fixedNumber,
                full: _that.full,
                canMoveBox: _that.canMoveBox,
                original: _that.original,
                centerBox: _that.centerBox,
                infoTrue: _that.infoTrue,
            }
            if (file instanceof File) {
                _that.dialogVisible = true
                this.fileToDataUrl(file, (value) => {
                    _that.option.img = value
                    _that.fileName = file.name
                })
            } else {
                _that.$message.warning("File");
            }
        },
        closeDialog() {
            Object.assign(this.$data, this.$options.data())
        },
        changeFixedType(value) {
            let arr = []
            if (value === 1) {
                arr = [16, 9]
                this.option = {
                    ...this.option,
                    fixed: true,
                    fixedNumber: arr,
                }
            } else if (value === 2) {
                arr = [4, 3]
                this.option = {
                    ...this.option,
                    fixed: true,
                    fixedNumber: arr,
                }
            } else if (value === 3) {
                arr = [1, 1]
                this.option = {
                    ...this.option,
                    fixed: true,
                    fixedNumber: arr,
                }
            } else {
                this.option = {
                    ...this.option,
                    fixed: false,
                    fixedNumber: [16, 9]
                }
            }
            
            // 
            this.$refs.cropper.refresh()
        },
        //File/Blobbase64dataURL
        fileToDataUrl(file, callback) {
            var reader = new FileReader();
            reader.onload = function() {
                callback(reader.result);
            };
            reader.readAsDataURL(file);//FileReaderBlobFiledataURL
        },
        finish() {
            this.loading = true
            this.$refs.cropper.getCropBlob((data) => {
                // , 
                this.$emit('changeImgMsg', { blobFile: data, name: this.fileName }, () => {
                    this.loading = false
                    this.closeDialog()
                })
                // fetch(`https://upload.wedengta.com/shutu/${new Date().getTime()}.png`, {
                //     method: 'post',
                //     headers: {
                //         'Content-Type': 'multipart/form-data',
                //     },
                //     body: data,
                // }).then(response => response.json()).then((res) => {
                //     if (res && res.msg === 'success') {
                //         this.loading = true
                //         this.$emit('changeImgMsg', { url: res.exurl, name: this.fileName })
                //         this.closeDialog()
                //     } else {
                //         this.$message.error('')
                //     }
                // })
            })
        }
    }
}
</script>
<style lang="less" scoped>
@import "./index.less";
</style>
